import React from "react";
import {Form, Alert,Input, Button, Checkbox, PageHeader,InputNumber} from 'antd';
import {Row, Col, message} from 'antd';
import axios from "axios";
import {useLocation, useNavigate} from "react-router-dom";


class ShareComponent extends React.Component {
    formRef = React.createRef();
    onFinish(values){
        console.log(values)
        let data = new FormData();
        for (let k in values){
            data.append(k,values[k]);
        }
        data.append('uuid',this.props.state.uuid)
        axios.post("/api/share/new",data).then(res=>{
            // message.success(res.data);
            // message.success("s");
            // this.props.navigate('/login');
            this.setState({"url":res.data,shared:false})
        }).catch(e=>{
            message.error(e.toString()+" : "+e.response.data);
        })
    }

    constructor(props) {
        super(props);
        this.state = {url:props.state.url,shared:false}
    }
    render() {
        if (!this.props.state) {
            console.log(1)
            this.props.navigate('/images')
        }
        return (
            <div>
                <PageHeader
                    className="site-page-header"
                    onBack={() => this.props.navigate(-1)}
                    title="Share"
                    style={{marginLeft:"50px"}}
                    // subTitle="This is a subtitle"
                />,
                <Row style={{paddingTop: '10em'}}>
                    <Col span={8} offset={4}>
                        <img style={{width: "400px"}} src={this.state.url} alt={"none"}/>
                    </Col>
                    <Col span={8} >
                        <Form
                            name="basic"
                            labelCol={{ span: 4 }}
                            wrapperCol={{ span: 8 }}
                            initialValues={{ x0: 0,y0:0 ,x1:0,y1:0,width:0,height:0,thumbnail:false,blur:0,text:"",textsize:0,textcolor:"FFFFFF"}}
                            onFinish={this.onFinish.bind(this)}
                            ref={this.formRef}
                            // onFinishFailed={onFinishFailed}
                            autoComplete="off"
                        >
                            <Form.Item
                                label="X0"
                                name="x0"
                                // rules={[{ required: true, message: 'Please input your username!' }]}
                            >
                                <InputNumber />
                            </Form.Item>
                            <Form.Item
                                label="Y0"
                                name="y0"
                                // rules={[{ required: true, message: 'Please input your username!' }]}
                            >
                                <InputNumber />
                            </Form.Item>
                            <Form.Item
                                label="X1"
                                name="x1"
                                // rules={[{ required: true, message: 'Please input your username!' }]}
                            >
                                <InputNumber />
                            </Form.Item>
                            <Form.Item
                                label="Y1"
                                name="y1"
                                // rules={[{ required: true, message: 'Please input your username!' }]}
                            >
                                <InputNumber />
                            </Form.Item>
                            <Form.Item
                                label="Width"
                                name="width"
                                // rules={[{ required: true, message: 'Please input your username!' }]}
                            >
                                <InputNumber />
                            </Form.Item>
                            <Form.Item
                                label="Height"
                                name="height"
                                // rules={[{ required: true, message: 'Please input your username!' }]}
                            >
                                <InputNumber />
                            </Form.Item>
                            <Form.Item
                                name="thumbnail"
                                wrapperCol={{ offset: 4, span: 8 }}
                                valuePropName="checked"
                                // rules={[{ required: true, message: 'Please input your username!' }]}
                            >
                                <Checkbox>Keep Ratio</Checkbox>
                            </Form.Item>
                            <Form.Item
                                label="Blur Radius"
                                name="blur"
                                // rules={[{ required: true, message: 'Please input your username!' }]}
                            >
                                <InputNumber />
                            </Form.Item>
                            <Form.Item
                                label="Text"
                                name="text"
                                // rules={[{ required: true, message: 'Please input your username!' }]}
                            >
                                <Input/>
                            </Form.Item>
                            <Form.Item
                                label="Text Size"
                                name="textsize"
                                // rules={[{ required: true, message: 'Please input your username!' }]}
                            >
                                <InputNumber
                                    defaultValue="1"
                                    min="0"
                                    max="1000"
                                    step="0.5"
                                    stringMode
                                />
                            </Form.Item>
                            <Form.Item
                                label="Text Color"
                                name="textcolor"
                                // rules={[{ required: true, message: 'Please input your username!' }]}
                            >
                                <Input placeholder={"#ffffff"} />
                            </Form.Item>
                            <Form.Item wrapperCol={{ offset: 4, span: 8 }}>
                                <Button  htmlType="submit">
                                    Preview
                                </Button>
                            </Form.Item>
                            <Form.Item wrapperCol={{ offset: 4, span: 8 }}>
                                <Button type="primary" onClick={()=>{
                                    let values = this.formRef.current.getFieldsValue(true)
                                    let data = new FormData();
                                    for (let k in values){
                                        data.append(k,values[k]);
                                    }
                                    data.append('uuid',this.props.state.uuid)
                                    data.append('public','1');
                                    axios.post("/api/share/new",data).then(res=>{
                                        message.success("Share success");

                                        this.setState({"url":res.data,shared:true})
                                    }).catch(e=>{
                                        message.error(e.toString()+" : "+e.response.data);
                                    })
                                }}>
                                    Share
                                </Button>
                            </Form.Item>
                        </Form>
                        {
                            this.state.shared?(<Alert showIcon type="success" description={'Link: '+this.state.url} />):undefined
                        }
                    </Col>
                </Row>
                <Row>

                </Row>
            </div>
        )
    }
}

function Share(props) {
    const navigate = useNavigate();
    const state = useLocation();
    console.log(state)
    return <ShareComponent {...props} navigate={navigate} state={state.state}/>
}

export default Share;