import React from "react";
import {Form, Input, Button} from 'antd';
import {Row, Col,message} from 'antd';
import axios from "axios";
import { useNavigate } from "react-router-dom";


class LoginComponent extends React.Component {
    onRegister(values){
        let data = new FormData();
        for (let k in values){
            data.append(k,values[k]);
        }
        axios.post("/api/user/login",data).then(res=>{
            console.log(123)
            message.success(res.data);
            this.props.navigate('/images');
        }).catch(e=>{
            message.error(e.toString()+" : "+e.response.data);
        })
    }

    render() {
        return (
            <div>
            <Row type='flex' justify='center' align='middle' style={{paddingTop: '10em'}}>
                <h1>Login</h1>
            </Row>
                <Row type='flex' justify='center' align='middle' style={{paddingTop: '1em'}}>
                <Col>
                    <Form
                        name="basic"
                        labelCol={{span: 8,}}
                        wrapperCol={{span: 16,}}
                        autoComplete="off"
                        onFinish={this.onRegister.bind(this)}
                    >
                        <Form.Item
                            label="Username"
                            name="username"
                            rules={[{ required: true, message: 'Please input your username!' }]}
                        >
                            <Input />
                        </Form.Item>

                        <Form.Item
                            label="Password"
                            name="password"
                            rules={[{ required: true, message: 'Please input your password!' }]}
                        >
                            <Input.Password />
                        </Form.Item>

                        <Form.Item
                            wrapperCol={{
                                offset: 4,
                                span: 16,
                            }}
                        >
                            <Button type="primary" htmlType="submit">
                                Login
                            </Button>
                            <Button onClick={() => {
                                this.props.navigate("/register")
                            }}>
                                Go Register
                            </Button>
                        </Form.Item>
                    </Form>
                </Col>
            </Row>
            </div>
        );
    }
}
function Login(props) {
    let navigate = useNavigate();
    return <LoginComponent {...props} navigate={navigate} />
}
export default Login;