import React from "react";
import {Button, Image} from 'antd';
import {Row, Col, message} from 'antd';
import axios from "axios";
import {Card} from 'antd';
import {Link, useNavigate} from "react-router-dom";
import {Layout, Menu, Upload} from 'antd';
import { UploadOutlined } from '@ant-design/icons';
const {Header, Content, Footer} = Layout;
const mock = ['https://i.loli.net/2021/11/08/qeDwd94TyCL8PcB.jpg', 'https://i.loli.net/2021/11/08/Py8FLVmsxIH2KG4.jpg']



class ImagesComponent extends React.Component {
    refreshImagesList() {
        axios.get("/api/image/list").then(res=>{
            // message.success("refresh success");
            this.setState({images:res.data});
        }).catch(e=>{
            if(e.response){
                if(e.response.status===401){
                    this.props.navigate('/login');
                }else{
                    message.error(e.response.data);
                }
            }else{
                message.error(e.toString());
            }
        })
        return mock
    }
    onUploadChange(info) {
        if (info.file.status !== 'uploading') {
            console.log(info.file, info.fileList);
        }
        if (info.file.status === 'done') {
            message.success(`${info.file.name} file uploaded successfully`);
        } else if (info.file.status === 'error') {
            message.error(`${info.file.name} file upload failed.`);
        }
        this.refreshImagesList()
    }
    constructor() {
        super();
        this.state = {
            images: []
        }
        this.refreshImagesList()
    }

    render() {
        return (
            <Layout className="layout">
                <Header>
                    <div className="logo"/>
                    <Menu theme="dark" mode="horizontal" defaultSelectedKeys={['1']}>
                        <Menu.Item key={1}>{"Images"}</Menu.Item>
                        <Menu.Item key={2} onClick={() => {
                            this.props.navigate("/sharelist")
                        }
                        }>{"ShareList"}</Menu.Item>
                        <Menu.Item key={3} onClick={() => {
                            this.props.navigate("/login")
                        }
                        }>{"Logout"}</Menu.Item>
                    </Menu>

                </Header>
                <Content style={{padding: '0 50px'}}>
                    <div className="site-layout-content">
                        <Row>
                            <Upload action={'/api/image/upload'} name={'image'} onChange={this.onUploadChange.bind(this)}>
                                <Button icon={<UploadOutlined/>}>Click to Upload</Button>
                            </Upload>
                        </Row>
                        <Row gutter={16} justify='center' style={{paddingTop: '1em'}}>
                            {
                                this.state.images.map(i => {
                                    return (

                                        <Col key={i.uuid}>
                                            <Card
                                                extra={<Link to="/share" state={{uuid: i.uuid, url: i.preview_url}}>Share</Link>}>
                                                <Image width={200} src={i.preview_url} preview={false}> </Image>
                                            </Card>
                                        </Col>
                                    )
                                })
                            }
                        </Row>
                    </div>
                </Content>
                <Footer style={{textAlign: 'center'}}>Images Services</Footer>
            </Layout>
        );
    }
}

function Images(props) {
    let navigate = useNavigate();
    return <ImagesComponent {...props} navigate={navigate}/>
}

export default Images;