import React from "react";
import {Button, Image, Input} from 'antd';
import {Row, Col, message} from 'antd';
import axios from "axios";
import {Card} from 'antd';
import { useNavigate, } from "react-router-dom";
import {Layout, Menu} from 'antd';

const {Header, Content, Footer} = Layout;

class ShareListComponent extends React.Component {

    constructor() {
        super();
        this.state = {
            sharedImages: []
        }
    }
    searchShareList(username){
        let data = new FormData();
        data.append('username',username)
        axios.post("/api/share/list",data).then(res=>{
            // message.success("refresh success");
            this.setState({sharedImages:res.data});
        }).catch(e=>{
            if(e.response){
                if(e.response.status===401){
                    this.props.navigate('/login');
                }else{
                    this.setState({sharedImages:[]});
                    message.error(e.response.data);
                }
            }else{
                message.error(e.toString());
            }
        })
    }
    render() {
        return (
            <Layout className="layout">
                <Header>
                    <div className="logo"/>
                    <Menu theme="dark" mode="horizontal" defaultSelectedKeys={['2']}>
                        <Menu.Item key={1} onClick={() => {
                            this.props.navigate("/images")
                        }
                        }>{"Images"}</Menu.Item>
                        <Menu.Item key={2}>{"ShareList"}</Menu.Item>
                        <Menu.Item key={3} onClick={() => {
                            this.props.navigate("/login")
                        }
                        }>{"Logout"}</Menu.Item>
                    </Menu>

                </Header>
                <Content style={{padding: '0 50px'}}>

                    <div className="site-layout-content">
                        <Row>
                            <Input.Search placeholder="username" style={{ width: 200 }} onSearch={this.searchShareList.bind(this)} />
                        </Row>
                        <Row gutter={16} justify='center' style={{paddingTop: '1em'}}>
                            {
                                this.state.sharedImages.map((i,index) => {
                                    return (
                                        <Col key={i+index.toString()}>
                                            <Card extra={<Button type="text">
                                                CopyLink
                                            </Button>}>
                                                <Image width={200} src={i} preview={false}> </Image>
                                            </Card>
                                        </Col>
                                    )
                                })
                            }
                        </Row>
                    </div>
                </Content>
                <Footer style={{textAlign: 'center'}}>Images Services</Footer>
            </Layout>
        );
    }
}

function ShareList(props) {
    let navigate = useNavigate();
    return <ShareListComponent {...props} navigate={navigate}/>
}

export default ShareList;